.wrapper {
  display: flex;
  justify-content: center;
  flex: 1 0 calc(100% / 5 * 2);
  max-width: 384px;
  height: 100%;
  padding: 0 64px 0 32px;
  border-left: 1px solid #eeeeee;

  @media (max-width: 1024px) {
    max-width: 100%;
    padding: 0 32px 32px 32px;
    // flex: 0 0 50%;
  }
}
